import React from 'react' 
import DarkModeOutlinedIcon from '@mui/icons-material/DarkModeOutlined';
import LightModeOutlinedIcon from '@mui/icons-material/LightModeOutlined';
import { connect } from 'react-redux';
import * as actions from '../../store/action'

const Top = props => { 
    const modeSwitcherStyle = { 
        color : props.darkMode ? 'white' : 'black'
    }
    return (
        <div className='top' > 
            <h1 className='top__title'>
                Meet Cipher 
            </h1>
            <button className='top__theme-toggler no-outline-border'>
                {
                    
                    props.darkMode ? 
                    <LightModeOutlinedIcon style ={modeSwitcherStyle} onClick={props.toLightMode} /> : 
                    <DarkModeOutlinedIcon style ={modeSwitcherStyle} onClick={props.toDarkMode} />   
                }
            </button>
        </div>
    )   
}   



const mapStateToProps = state => { 
    return { 
        darkMode : state.theme.darkMode , 
    }
}


const mapDispatchToProps= dispatch => { 
    return {  
        toDarkMode : (mode)=> dispatch({ 
            type : actions.CHANGE_THEME_MODE , 
            mode : 'dark'
        }), 
        toLightMode : mode => dispatch ({ 
            type : actions.CHANGE_THEME_MODE , 
            mode : 'light'
        })
    }
}

export default connect(mapStateToProps , mapDispatchToProps) (Top)