import React from 'react' 


export const Home = props => { 
    return (
        <React.Fragment>
        <div className='home'>
            <div className='home__greeting'> 
                Hello 👋,<br /> 
                Welcome to my portfolio site 🚀 
            </div>
            <div className='home__name'>
                <h1>
                    I'm <span style={{translate : 'no'}}>Samuel Yakubu</span>
                </h1>
                <span>cipher(socials)</span>
            </div>

            <ul className='home__roles'>
                <li>Frontend developer</li>
                <li>React.js freak </li>
            </ul>
        </div>
        <About /> 
        </React.Fragment>
    )
}

const About = ()=> { 
    return (
        <div className='about'> 
            <h1 className='about__title s-title'>About Me</h1>
            <div className='about__content'>
            <p>
            I am a frontend developer and an undergraduate, Department of Computer Science, FUT Minna. I'm currently in my second year of studies.
            </p> 
            <p>
            I use web development technologies ranging from HTML, CSS, SASS, Bootstrap, to JavaScript and Version Control System (Git) to develop responsive, dynamic websites.
            </p>
            <p>
            By utilizing the power of SASS mixins, utility classes and components, I ensure that my frontend codes remains DRY and readable.
            </p>
            <p>
            Aside tech related activities, I've developed passion for soccer and pc games.
            </p> 

            I am <span className='important'>OPEN</span> to frontend development roles.
            </div> 
             
        </div>
    )
}