import * as actions from './action'

const isDarkMode = (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) ? true : false  

const initialState = { 
    theme : { 
        darkMode : isDarkMode , 
        styles : { 
            backgroundColor : isDarkMode ? 'hsl(240, 17%, 12%)': 'hsl(96, 71%, 99%)' ,
            color : isDarkMode ? 'hsl(96, 71%, 99%)' : 'hsl(240, 17%, 12%)'
        }

    }  
}


export const reducer = (state = initialState , action ) => { 
    switch( action.type) { 
        case actions.CHANGE_THEME_MODE : 
            const mode =  action.mode === 'dark' ;
             return { 
                ...state , 
                theme : { 
                    darkMode : mode  , 
                    styles : { 
                        backgroundColor : mode ? 'hsl(240, 17%, 12%)': 'hsl(96, 71%, 99%)' ,
                        color : mode ? 'hsl(96, 71%, 99%)' : 'hsl(240, 17%, 12%)'
                    }
                }
             }
        default : 
        return state 
    }
} 