import React from "react"; 
import { connect } from "react-redux";

import { NavLink } from "react-router-dom";

import 
{ 
  ArticleOutlined , 
  HomeOutlined , 
  PermContactCalendarOutlined as Contact
} from '@mui/icons-material';


const BottomNav = props => { 
    return ( 
        <div className="bottomNav" style={props.theme.styles}>
            <NavLink to='/' className="bottomNav__item" exact>
                <HomeOutlined /> 
                <span className='bottomNav__item--text'> Home </span>
            </NavLink>

            <NavLink to='/projects' className="bottomNav__item">
                <HomeOutlined /> 
                <span className='bottomNav__item--text'> Projects </span>
            </NavLink>

            <NavLink to='/articles' className="bottomNav__item">
                <ArticleOutlined /> 
                <span className='bottomNav__item--text'> Articles</span>
            </NavLink>



         
            <NavLink to='/contact' className="bottomNav__item">
                <Contact /> 
                <span className='bottomNav__item--text'> Contact </span>
            </NavLink>

        </div>
        )
}


const mapStateToProps = state => { 
    return { 
        theme : state.theme
    }
}

export default connect(mapStateToProps)(BottomNav)