import React from 'react'

export const Project = props => { 
    return ( 
        <div className='project'> 
            <div className='project__image'> 
                <img src={props.imgSrc} alt={props.imgAltText} />
            </div>
        <div className='project__content'> 
            <h1 className='project__content--title'>
                {props.title}
            </h1>
            <div className='project__content--description'>
               {props.description} 
            </div>
            <div className='project__content--cta'>
                <a href={props.sourceCodeURL} className='cta cta--source'>SOURCE CODE </a>
                <a href={props.liveURL} className='cta cta--live'>GO LIVE </a>
            </div>
        </div>
        </div>
    )
}