import React from 'react' ; 
import Manage from './manage.png'
import {Project} from './Project/Project'

export const Projects = props => { 
    const projects = [ 
        {
            title : 'Manage Landing Page', 
            description : 'A landing page made with  HTML5, CSS, SASS and JavaScript. It was a challenge from frontend mentor. ' , 
            liveURL : './#' , 
            sourceCodeURL : './#' , 
            imgSrc : Manage , 
            imgAltText : 'Manage Landing Page' , 

        },
        {
            title : 'Manage Landing Page2', 
            description : 'A landing page2' , 
            liveURL : './#' , 
            sourceCodeURL : './#' , 
            imgSrc : Manage , 
            imgAltText : 'Manage Landing Page' , 

        },
        {
            title : 'Manage Landing Page3', 
            description : 'A landing page3' , 
            liveURL : '/#' , 
            sourceCodeURL : '/#' , 
            imgSrc : Manage , 
            imgAltText : 'Manage Landing Page' , 

        },
    ]
    return ( 
        <div className='projects-block'>
            <h1 className='s-title'>Things I've built</h1>
            
            <div className='projects'> 
               { 
                projects.map( project=> (
                <Project key={project.title}
                        {...project}
                        />
                ))
               }
                

            </div>
        </div>
    )
}